import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { Component, getAssetPath, h, Host, Prop } from '@stencil/core';
import { BsImage } from '../image/image';

/**
 * The Quote component is used for indicating that a particular section of text is being taken from another website or other source.
 */
@Component({
  tag: 'bs-story-quote',
  styleUrl: 'story-quote.scss',
  shadow: true,
})
export class BsStoryQuote {
  /**
   * The quote text copied from an external source.
   */
  @Prop() text!: string;

  /**
   * Author's name
   */
  @Prop() authorName: string;

  /**
   * Author's job position
   */
  @Prop() authorPosition: string;

  /**
   * A URL that designates a source document or message for the information quoted. Used for SEO purposes.
   */
  @Prop() source?: string;

  /**
   *  Prop for adding an image
   */
  @Prop() authorImage: BsImage | string;

  @Memoize('authorImage')
  get parsedAuthorImage(): BsImage {
    return parseIfString(this.authorImage);
  }

  render() {
    const schema = {
      '@context': 'https://schema.org/',
      '@type': 'Quotation',
      creator: {
        '@type': 'Person',
        name: this.authorName,
      },
      text: this.text,
    };

    return (
      <Host>
        <blockquote class="blockquote" cite={this.source}>
          <p class="quote-mark">
            <ion-icon class="icon" aria-hidden="true" src={getAssetPath(`assets/quote.svg`)} />
          </p>
          <p class="quote">&quot;{this.text}&quot;</p>
          <div class="info">
            {/* Image */}
            <div class="author-image">
              <bs-image class="image" {...this.parsedAuthorImage} cover />
            </div>
            {/* Author info */}
            <dl class="author-info">
              <dt>{this.authorName}</dt>
              <dd>{this.authorPosition}</dd>
            </dl>
          </div>
        </blockquote>

        <script type="application/ld+json">{JSON.stringify(schema)}</script>
      </Host>
    );
  }
}
