:host .blockquote {
  margin: 0;

  .quote-mark {
    margin-bottom: rem(15);
    text-align: center;
  }

  .icon {
    min-width: rem(45);
    height: rem(30);

    @include mq('max', 'md') {
      width: rem(25);
      height: rem(15);
    }
  }

  .quote {
    margin-top: rem(10);
    margin-bottom: rem(16);
    font-size: font-size(large);
    text-align: center;

    @include mq('min', 'md') {
      font-size: font-size(huge);
    }
  }
}

:host .author-image {
  position: relative;
  flex-shrink: 0;
  width: rem(50);
  height: rem(50);

  overflow: hidden;
  border-radius: 50%;

  .image {
    @include cover;
  }

  @include mq('max', 'md') {
    width: rem(32);
    height: rem(32);
  }
}

:host .author-info {
  dt {
    font-size: font-size(default);
    font-weight: font-weight(bold);
    letter-spacing: font-spacing(loose);

    @include mq('min', 'md') {
      font-size: font-size(x-large);
    }
  }

  dd {
    margin: 0;
    font-size: font-size(small);
    color: $gray-700;

    @include mq('min', 'md') {
      font-size: font-size(medium);
    }
  }

  @include mq('max', 'md') {
    margin-top: rem(10);
    text-align: center;
  }
}

:host .info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: rem(16);

  @include mq('min', 'md') {
    flex-direction: row;
    gap: rem(16);
    margin-top: rem(25);
  }
}
